import uniPagination from '@/components/uni-pagination/uni-pagination.vue'
import EPassPicker from '@/components/EPass-picker.vue'
import config from '@/common/config.js'
import req from '@/common/req.js'
export default {
	components: {
		uniPagination,
		EPassPicker
	},
	data() {
		return {
			jg: "", //选择籍贯绑定变量

			// checkbox是否租客 默认false
			isCheckZuke: false,

			// 双向绑定数据-用于数据回显
			xm: '', //姓名
			zjhm: '', //证件号码
			lxdh: '', //联系电话

			// 是否租客
			isTenant: 0,

			//出发地
			cfd: '',
			// 选项中没有我的房屋地址 默认false
			isHomeAddress: false,

			// 是否在穗checkbox 默认false
			isCheckSui: false,
			alwaysInGz: 0, //是否在穗1  否0
			date: this.$custom.getYMD(),
			returnTime: this.$custom.getYMD(), //返穗日期

			// +--------------------
			// |返穗交通工具下拉数据集合
			// +--------------------
			vehicleList: ['大巴', '火车', '自驾', '飞机', '其他'],
			vehicle: "", //反穗交通提交字段
			vehicleIndex: -1, //下拉下标

			// 切换modal显示
			modalName: null,

			// 街镇下拉框
			jzDropList: [],
			jzList: [],
			jzCode: '',
			jzName: "",
			jzIndex: -1,

			// 居委会下拉框
			jwhDropList: [],
			jwhList: [],
			jwhCode: '',
			jwhName: "",
			jwhIndex: -1,

			// 街路巷参数
			jddm: '', //街路巷编码
			jddmName: '', //街路巷名称
			jddmSearch: '', //街路巷搜索名称

			// 门牌
			mp: '',
			mph: '',
			mpName: '',
			mpSearch: '',

			// 门牌号
			dong: '',
			dongName: '',
			dongSearch: '',

			// 单元
			tao: '',
			taoName: '',
			taoSearch: '',

			// 分页切换按钮状态
			canFetch: true,

			// 分页数据集合
			jddmList: [],
			currentPage: 1, //当前页数
			total: 0, //总页数
			pageSize: 20, //每页显示数据行数
			status: '', //分页名字状态，用于判断当前分页显示的什么地址数据

			// 登记数据
			registerData: "",

			// 授权对象
			sqdxIndex: -1,
			sqdx: -1,
			sqdxName: '',
			sqdxList: [{
					value: 1,
					name: '出租屋租客'
				},
				{
					value: 2,
					name: '出租屋房东'
				},
				{
					value: 0,
					name: '自住业主'
				},
			]
		};
	},
	onLoad() {
		// 获取登记数据
		let registerData = this.$store.state.registerData;
		// 回显数据
		if (!this.$custom.isEmpty(registerData)) {
			// 数据回显
			this.dataDisplay(registerData);
		} else {
			this.registerData = "";
		}
		// 初始加载街镇
		this.getDropJz();
	},
	created() {
		// this.getJwh();
	},
	methods: {
		// 回显数据
		dataDisplay(registerData) {
			this.registerData = registerData[0];
			this.xm = this.registerData.xm; //姓名
			this.zjhm = this.registerData.zjhm; //证件号码
			this.lxdh = this.registerData.lxdh; //联系电话
			this.jg = this.registerData.jg
			this.cfd = this.registerData.departAddress
		},
		// 提交请求
		formSubmit(e) {
			let data = e.detail.value;
			console.log(data);
			if (this.$custom.isEmpty(data.xm)) {
				this.$refs['Message'].error("请输入姓名");
				return false;
			};
			if (this.$custom.isEmpty(data.zjhm)) {
				this.$refs['Message'].error("请输入身份证号");
				return false;
			};
			if (/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(data.zjhm) === false) {
				this.$refs['Message'].error("身份证输入不合法");
				return false;
			};
			if (this.$custom.isEmpty(data.jg)) {
				this.$refs['Message'].error("请选择籍贯");
				return false;
			};
			if (this.$custom.isEmpty(data.lxdh)) {
				this.$refs['Message'].error("请输入联系电话");
				return false;
			};
			if (!(/^1[3|4|5|6|7|8|9]\d{9}$/.test(data.lxdh) || /^0\d{2,3}-?\d{7,8}$/.test(data.lxdh))) {
				this.$refs['Message'].error("请输入正确的联系电话");
				return false;
			};
			//如果是租客 1租客  0 不是租客
			if (data.isTenant == 1) {
				if (data.renterName == '') {
					this.$refs['Message'].error("请输入房东姓名");
					return false;
				};
				if (data.renterPhone == '') {
					this.$refs['Message'].error("请输入房东电话");
					return false;
				};
				if (!(/^1[3|4|5|6|7|8|9]\d{9}$/.test(data.renterPhone) || /^0\d{2,3}-?\d{7,8}$/.test(data.renterPhone) ||
						/^(0\d{2,3}\-)?([2-9]\d{6,7})+(\-\d{1,6})?$/.test(data.renterPhone))) {
					this.$refs['Message'].error("请输入正确的房东电话");
					return false;
				};
			}

			if (this.$custom.isEmpty(this.jzCode)) {
				this.$refs['Message'].error("请选择街镇");
				return false;
			};
			if (this.$custom.isEmpty(this.jwhCode)) {
				this.$refs['Message'].error("请选择居委会");
				return false;
			};
			// 如果没有选择居住详址
			if (!this.isHomeAddress) {
				data.jddm = this.jddm;
				data.jddmName = this.jddmName;
				data.mpid = this.mpid;
				data.mph = this.mph;
				data.dy = this.taoName;
				data.mpmc = this.mph;
				data.zhid = this.mpid;
				data.fhid = this.tao;
				if (this.$custom.isEmpty(this.jddm)) {
					this.$refs['Message'].error("请选择街路巷");
					return false;
				};
				if (this.$custom.isEmpty(this.mph)) {
					this.$refs['Message'].error("请选择门牌号");
					return false;
				};
				if (this.$custom.isEmpty(this.taoName)) {
					this.$refs['Message'].error("请选择单元");
					return false;
				};
			} else {
				if (this.$custom.isEmpty(data.selfCompiledAddress)) {
					this.$refs['Message'].error("请填写居住详址");
					return false;
				};
			}
			if (this.$custom.isEmpty(this.sqdx)) {
				this.$refs['Message'].error("请选授权对象");
				return false;
			};
			// 如果没有勾选一直在穗,就验证   1在穗  0不在穗
			if (data.alwaysInGz == 0) {
				if (this.$custom.isEmpty(this.date)) {
					this.$refs['Message'].error("请选择返穗日期");
					return false;
				};
				if (this.$custom.isEmpty(data.departAddress)) {
					this.$refs['Message'].error("请输入出发地");
					return false;
				};
				if (this.$custom.isEmpty(data.vehicle)) {
					this.$refs['Message'].error("请选择返穗交通工具");
					return false;
				};
			} else {
				// 清空反穗日期
				data.returnTime = "";
			}
			// 证件号码转大写
			data.zjhm = data.zjhm.toUpperCase();


			// openid
			data.openId = this.$store.state.openId;
			// 加载动画
			uni.showLoading({});
			
			req.http('save', data, 'post').then(data => {
				uni.hideLoading();
				if (data.appCode == 1) {
					this.$refs['Message'].success("登记成功");
					// 保存openid到状态
					this.$store.dispatch('refreshRegisterStatus', 1);
					let timeout = setTimeout(() => {
						// 如果是管理员
						if (this.$store.state.userType == 1) {
							this.$routes.redTo("/pages/index/manager");
						} else {
							this.$routes.redTo("/pages/index/index");
						}
						clearTimeout(timeout);
					}, 2000);
				} else {
					this.$refs['Message'].error(data.appMsg);
				}
			});
			// let _this = this;
			//如果数据为空是首次登记
		// 	if (this.$custom.isEmpty(this.registerData)) {
		// 		// 发起请求
		// 		req.http('save', data, 'post').then(data => {
		// 			uni.hideLoading();
		// 			if (data.appCode == 1) {
		// 				this.$refs['Message'].success("登记成功");
		// 				// 保存openid到状态
		// 				this.$store.dispatch('refreshRegisterStatus', 1);
		// 				let timeout = setTimeout(function() {
		// 					// 如果是管理员
		// 					if (_this.$store.state.userType == 1) {
		// 						_this.$routes.redTo("/pages/index/manager");
		// 					} else {
		// 						_this.$routes.redTo("/pages/index/index");
		// 					}
		// 					clearTimeout(timeout);
		// 				}, 2000);
		// 			} else {
		// 				_this.$refs['Message'].error(data.appMsg);
		// 			}
		// 		});
		// 	} else {
		// 		// 增加识别id
		// 		data.id = this.registerData.id;
		// 		req.http('updateMyPassReport', data, 'post').then(data => {
		// 			uni.hideLoading();
		// 			if (data.appCode == 1) {
		// 				this.$refs['Message'].success("登记成功");
		// 				// 保存登录到状态
		// 				this.$store.dispatch('refreshRegisterStatus', 1);
		// 				let timeout = setTimeout(function() {
		// 					// 如果是管理员
		// 					if (_this.$store.state.userType == 1) {
		// 						_this.$routes.redTo("/pages/index/manager");
		// 					} else {
		// 						_this.$routes.redTo("/pages/index/index");
		// 					}

		// 					clearTimeout(timeout);
		// 				}, 2000);
		// 			} else {
		// 				_this.$refs['Message'].error(data.appMsg);
		// 			}
		// 		});
		// 	}
		},
		// 获取街镇下拉街镇
		async getDropJz() {
			let result = await this.$request.post(this.$api.getJz);
			if (result.appCode == 1) {
				let resultDropList = [];
				result.data.forEach(item => {
					resultDropList.push(item.jzmc)
				});
				// 下拉框数据
				this.jzDropList = resultDropList;
				// 下拉集合
				this.jzList = result.data;
			}

		},
		// 获取居委会下拉街镇
		async getDropJwh() {

			let result = await this.$request.post(this.$api.getJwh, {
				"jz": this.jzCode
			});
			if (result.appCode == 1) {
				let resultDropList = [];
				result.data.forEach(item => {
					resultDropList.push(item.jwhmc)
				});
				// 下拉框数据
				this.jwhDropList = resultDropList;
				// 下拉集合
				this.jwhList = result.data;
			}
		},
		// 街路巷输入清空
		changeJlx() {
			this.jddmName = '';
			this.jddm = '';
			this.mpName = '';
			this.mp = '';
			this.dong = '';
			this.dongName = '';
			this.tao = '';
			this.taoName = '';
			this.dy = '';
		},
		// 门牌号输入清空
		changeDong() {
			this.mpName = '';
			this.mp = '';
			this.dong = '';
			this.dongName = '';
			this.tao = '';
			this.taoName = '';
			this.dy = '';
		},
		// 单元输入清空
		changeTao() {
			this.tao = '';
			this.taoName = '';
			this.dy = '';
		},
		// 选择分页行的地址
		selectJddm(item) {
			this.modalName = null; //隐藏分页modal
			// 如果是街路巷
			if (this.status == 'jddm') {
				this.jddm = item.itemCode;
				this.jddmName = item.itemName;
				this.jddmSearch = '';
				// 清空门牌号
				this.mp = '';
				this.mph = '';
				this.mpSearch = '';
				// 清空栋
				this.dong = '';
				this.dongName = '';
				this.dongSearch = '';
				// 清空套
				this.tao = '';
				this.taoSearch = '';
				this.taoName = '';
			}
			// 如果是门牌
			if (this.status == 'mp') {
				this.mp = item.mpid;
				this.mph = item.mph;
				this.mpName = item.mpmc;
				this.mpSearch = '';
				// 清空栋
				this.dong = '';
				this.dongName = '';
				this.dongSearch = '';
				// 清空套
				this.tao = '';
				this.taoSearch = '';
				this.taoName = '';
			}
			// 如果是栋
			if (this.status == 'dong') {
				this.dong = item.zhid;
				this.mp = item.mpid;
				this.mph = item.mph;
				// this.mph = item.mph;
				this.dongName = item.mpmc;
				this.mpSearch = '';
				// this.dong = '';
				// this.dongName = '';
				this.dongSearch = '';
				this.tao = '';
				this.taoSearch = '';
				this.taoName = '';
			}
			// 如果是套
			if (this.status == 'tao') {
				this.tao = item.fhid;
				this.dy = item.dy;
				// this.mph = item.mph;
				this.taoName = item.dy;
				this.taoSearch = '';
			}
		},
		// 重置分页参数
		resetPageStr() {
			// 置初始分页为1
			this.currentPage = 1;
			// 置总页数为0
			this.total = 0;
			// 清空分页数据
			this.jddmList = [];
		},
		// 搜索街路巷地址
		searchJddm() {
			if (this.$custom.isEmpty(this.jzCode)) {
				this.$refs['Message'].error("请选择街镇");
				return false;
			}
			// 重置分页参数
			this.resetPageStr();
			// 修改状态为jddm
			this.status = 'jddm';
			// 加载街路巷数据
			this.loadJddm();
		},
		// 加载街路巷数据
		async loadJddm() {
			uni.showLoading({});
			let data = {};
			data.pageSize = this.pageSize; //每页显示数据条数
			data.currentPage = this.currentPage; //起始页
			data.jddmName = this.jddmSearch; //模糊搜索搜索名称
			data.jz = this.jzCode; //街镇地址
			data.jwh = this.jwhCode; //居委会地址
			// 请求
			let result = await this.$request.post(this.$api.getJddm, data);
			if (result.appCode == 1) {
				this.total = result.count || 1;
				this.currentPage = result.currentPage || 1;
				this.jddmList = result.dataInfo || [];
				uni.hideLoading();
				// 判断是否有数据，点击下一页时会判断
				this.canFetch = true;
			}
			// 显示modal
			this.modalName = 'viewModal';
		},
		// ----搜索门牌号
		searchMp() {
			// 重置分页参数
			this.resetPageStr();
			// 更改状态
			this.status = 'mp';
			// 加载门牌数据
			this.loadMp();
		},
		// ----加载门牌号数据
		async loadMp() {
			uni.showLoading({});
			let data = {};
			data.pageSize = this.pageSize;
			data.currentPage = this.currentPage;
			data.mpmc = this.mpSearch;
			data.jddm = this.jddm;
			// 请求
			let result = await this.$request.post(this.$api.getMpid, data);
			if (result.appCode == 1) {
				this.total = result.count;
				this.currentPage = result.currentPage || 1;
				this.jddmList = data.dataInfo || [];
				uni.hideLoading();
				// 判断是否有数据，点击下一页时会判断
				this.canFetch = true;
			};
			// 显示modal
			this.modalName = 'viewModal';
		},
		// 搜索门牌号地址
		searchDong() {
			if (this.$custom.isEmpty(this.jddm)) {
				this.$refs['Message'].error("请先选择街路巷");
				return false;
			}
			// 重置分页参数
			this.resetPageStr();
			this.status = 'dong';
			this.loadDong();
		},
		// 加载门牌号地址
		async loadDong() {
			uni.showLoading({});
			let data = {};
			data.pageSize = this.pageSize;
			data.currentPage = this.currentPage;
			data.mpmc = this.dongSearch;	// 模糊搜索
			data.jddm = this.jddm;
			data.jwh = this.jwhCode;
			let result = await this.$request.post(this.$api.getZhid, data);
			if (result.appCode == 1) {
				this.total = result.count || 1;
				this.currentPage = result.currentPage || 1;
				this.jddmList = result.dataInfo || [];
				uni.hideLoading();
				this.canFetch = true;
			};
			this.modalName = 'viewModal';
		},
		// 搜索单元地址
		searchTao() {
			if (this.$custom.isEmpty(this.dong)) {
				this.$refs['Message'].error("请先选择门牌号");
				return false;
			}
			// 重置分页参数
			this.resetPageStr();
			this.status = 'tao';
			this.loadTao();
		},
		// 加载单元地址
		async loadTao() {
			uni.showLoading({});
			let data = {};
			data.pageSize = this.pageSize;
			data.currentPage = this.currentPage;
			data.dy = this.taoSearch;	// 模糊搜索
			data.zhid = this.dong;
			let result = await this.$request.post(this.$api.getFhid, data);
			if (result.appCode == 1) {
				this.total = result.count || 1;
				this.currentPage = result.currentPage || 1;
				this.jddmList = result.dataInfo || [];
				uni.hideLoading();
				this.canFetch = true;
			};
			this.modalName = 'viewModal';
		},
		// 获取来穗的人员信息, 传入人员的身份证
		async getPersonByZjhm() {
			uni.showLoading({
				title: '匹配数据中',
				mask: true
			});
			const {
				data,
				appCode
			} = await this.$request.post(this.$api.getPersonByZjhm, {
				"zjhm": this.zjhm
			});
			uni.hideLoading();
			if (appCode == 1) {
				if (data === '' || data === null) {
					return false
				}
				if (data.jz === this.jzCode) {
					// 居委会赋值
					this.jwhCode = data.jwh || ''
					this.jwhName = data.jwh_dict || ''
					// 居委会回显
					for (const [index, elem] of this.jwhList.entries()) {
						if (elem.jwhdm === data.jwh) {
							this.jwhIndex = index
						}
					}
					// 街路巷
					this.jddm = data.jddm || '';
					this.jddmName = data.jddm_dict || '';
					// 门牌号
					this.mpid = data.zhid || '';
					this.mph = data.mpmc || '';
					// 单元
					this.tao = data.fhid || '';
					this.taoName = data.dy || '';
				}
			}
		},
		// 点击分页
		jumpPage(e) {
			// 如果有数据
			if (this.canFetch) {
				this.canFetch = false;
				// 当前页码
				this.currentPage = e.current;
				// 加载街路巷
				if (this.status == 'jddm') this.loadJddm();
				// 门牌号
				if (this.status == 'mp') this.loadMp();
				// 门牌号
				if (this.status == 'dong') this.loadDong();
				// 单元
				if (this.status == 'tao') this.loadTao();
			}

		},

		//隐藏modal
		hideModal(e) {
			this.modalName = null;
		},
		//---------------下拉框事件开始------------
		bindDropChange(e) {
			console.log(e)
			let dropIndex = e.detail.value; //下标位置
			let dropName = e.currentTarget.dataset.name; //下拉框识别名称
			switch (dropName) {
				case 'date': //返穗日期
					this.date = dropIndex; //显示的值
					this.returnTime = dropIndex; //提交的值
					break;
				case 'vehicle': //返穗交通工具
					this.vehicle = parseInt(dropIndex) + 1; //提交的值
					this.vehicleIndex = dropIndex;
					break;
				case 'jz': //街镇
					this.jzIndex = dropIndex;
					this.jzCode = String(this.jzList[dropIndex]['jzdm']); //提交的值
					this.jzName = String(this.jzList[dropIndex]['jzmc']); //提交的值

					// 清空居委会
					this.jwhIndex = -1
					this.jwhCode = ''
					this.jwhName = ''

					// 加载居委会
					this.getDropJwh();

					// 清空街路巷
					this.jddm = '';
					this.jddmName = '';
					this.jddmSearch = '';
					// 清空门牌号
					this.mp = '';
					this.mph = '';
					this.mpSearch = '';
					// 清空单元
					this.dong = '';
					this.dongName = '';
					this.dongSearch = '';

					this.tao = '';
					this.taoSearch = '';
					this.taoName = '';

					// 使用请求来穗的数据
					this.getPersonByZjhm()
					break;
				case 'jwh': //居委会
					this.jwhIndex = dropIndex;
					this.jwhCode = String(this.jwhList[dropIndex]['jwhdm']); //提交的值
					this.jwhName = String(this.jwhList[dropIndex]['jwhmc']); //提交的值
					// 清空街路巷
					this.jddm = '';
					this.jddmName = '';
					this.jddmSearch = '';
					// 清空门牌号
					this.mp = '';
					this.mph = '';
					this.mpSearch = '';
					// 清空单元
					this.dong = '';
					this.dongName = '';
					this.dongSearch = '';

					this.tao = '';
					this.taoSearch = '';
					this.taoName = '';
					break;
				case 'sqdx': // 授权对象
					console.log(dropIndex)
					console.log(this.sqdxList[dropIndex])
					this.sqdxIndex = dropIndex
					this.sqdx = this.sqdxList[dropIndex].value
					this.sqdxName = this.sqdxList[dropIndex].name
					break;
			}

		},
		//---------------下拉框事件结束------------

		// --------------checkbox事件开始-----------
		//  是否有居住地址checkbox
		checkboxHomeChange(e) {
			let value = e.detail.value;
			// 1勾选  0没勾选
			this.isHomeAddress = value.length > 0 ? true : false;
		},
		//  一直在穗checkbox
		checkboxSuiChange(e) {
			let value = e.detail.value;
			// 1在穗  0不在穗
			this.isCheckSui = value.length > 0 ? 1 : 0;
			this.alwaysInGz = this.isCheckSui;
		},
		//  是否租客checkbox
		checkboxZukeChange(e) {
			let value = e.detail.value;
			// 1租客  0不是租客
			this.isCheckZuke = value.length > 0 ? 1 : 0;
			this.isTenant = this.isCheckZuke;
		},
		jddmSearchConfirm(e) {
			this.jddmSearch = e.detail.value
			this.searchJddm()
		},
		setChangePicker(val) {
			this[val.name] = val.ChangeArray
		}
	}
}
